import {
  Disclosure,
  DisclosureButton,
  DisclosurePanel,
} from "@headlessui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import React from "react";
import { Link } from "gatsby";

const ToggleDropdown = ({ title, isOpen, onToggle }) => {
  const getLinks = () => {
    if (title === "Blogs") {
      return [
        { name: "Blog", path: "/blog" },
        { name: "Blog Post", path: "/blog-post" },
      ];
    }
    return [
      { name: "Portfolio", path: "#portfolio" },
      { name: "Our Work", path: "#ourWork" },
      { name: "Testimonials", path: "/testimonials" },
      { name: "Service Areas", path: "/servicearea" },
      { name: "FAQs", path: "/#faqs" },
    ];
  };

  return (
    <div className="">
      <Disclosure as="div" className="" defaultOpen={isOpen}>
        {({ open }) => {
          const currentlyOpen = isOpen && open;

          return (
            <>
              <DisclosureButton
                className="disclosure_button_section"
                onClick={() => onToggle(title)}
              >
                <span>{title}</span>
                <FontAwesomeIcon
                  icon={faChevronDown}
                  className="chevron_icon"
                  style={{
                    transition: "transform 0.2s",
                    transform: currentlyOpen ? "rotate(180deg)" : "rotate(0deg)",
                  }}
                />
              </DisclosureButton>

              {isOpen && (
                <DisclosurePanel className="disclosure_panel_section">
                  {getLinks().map((link) => (
                    <Link key={link.name} className="link" to={link.path}>
                      {link.name}
                    </Link>
                  ))}
                </DisclosurePanel>
              )}
            </>
          );
        }}
      </Disclosure>
    </div>
  );
};

export default ToggleDropdown;
