import React, { useState } from "react";

import HeaderMenu from "./HeaderMenu";
import { TopHeader, MiddleHeader, BottomNav, HeaderWrapper } from "../Branding";

import { useLocation } from "@reach/router";
import {  contactItems, HeaderAddress, logo, socialIcons } from "../../Data/Content";


const Header = () => {
  
  const handleToggle = (title) => {
    setOpenDropdown((prev) => (prev === title ? null : title));
  };
  const toggleDrawer = () => {
    if (!isOpen) {
      // Opening
      setShowOverlay(true);
      setTimeout(() => setShowDrawer(true), 500);
    } else {
      // Closing
      setShowDrawer(false); // hide drawer first
      setTimeout(() => setShowOverlay(false), 1000);
    }
    setIsOpen(!isOpen);
  };
 
  const [isOpen, setIsOpen] = useState(false);
  const [showOverlay, setShowOverlay] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);
  const [openDropdown, setOpenDropdown] = useState(null);
  const location = useLocation();
    const currentPath = location.pathname + location.hash;
  
    const isActive = (path) => {
      return currentPath === path;
    };
  return (
    <>
   
    <HeaderWrapper
      top={<TopHeader addresses={HeaderAddress} socials={socialIcons} />}
      middle={
        <MiddleHeader
          logo={logo}
          contacts={contactItems}
          handleToggle={handleToggle}
          showOverlay={showOverlay}
          setShowOverlay={setShowOverlay}
          showDrawer={showDrawer}
          setShowDrawer={setShowDrawer}
          isOpen={isOpen}
          toggleDrawer={toggleDrawer}
          openDropdown={openDropdown}
          setOpenDropdown={setOpenDropdown}
          isActive={isActive}
          location={location}
        />
      }
      bottom={<BottomNav menu={<HeaderMenu  isActive={isActive} location={location}/>} />}
    />
    </>
  );
};

export default Header;
